const ServingTable = () => {
  const [expand, setExpand] = useState([]);

  const handleExpand = (id) => {
    setExpand([...expand, id]);
  };

  const handleCollapse = (id) => {
    setExpand(expand.filter((i) => i !== id));
  };

  return (
    <Container>
      <StyledTitle>  </StyledTitle>
      <ServingInfoTable>
        {servingData.map((r) => (
          <>
            <Divider />
            <ServingInfoRow collapsed={expand.includes(r.id) ? false : true}>
              <ServingInfoName>{r.title}</ServingInfoName>
              <ServingInfoButton>
                <AddIcon
                  style={{
                    color: "#5DB683",
                    weight: 200,
                    cursor: "pointer",
                    display: expand.includes(r.id) ? "none" : "block",
                  }}
                  onClick={() => handleExpand(r.id)}
                />
                <RemoveIcon
                  style={{
                    color: "#ffffff",
                    weight: 200,
                    cursor: "pointer",
                    display: expand.includes(r.id) ? "block" : "none",
                  }}
                  onClick={() => handleCollapse(r.id)}
                />
              </ServingInfoButton>
            </ServingInfoRow>
            <ServingInfoCollapse
              collapsed={expand.includes(r.id) ? false : true}
            >
              {r.content.map((item) => (
                <CollapseBigRow key={item.id}>
                  <CollapseSmallRow>
                    <CollapseTitle>
                      <NoLetter src={item.src} />
                      {item.title}
                    </CollapseTitle>
                  </CollapseSmallRow>
                  {item.content && (
                    <CollapseSmallRow>
                      <CollapseContent>{item.content}</CollapseContent>
                    </CollapseSmallRow>
                  )}
                </CollapseBigRow>
              ))}
            </ServingInfoCollapse>
          </>
        ))}
        <Divider />
      </ServingInfoTable>
    </Container>
  );
};

export default ServingTable;
