import { useState } from "react";
import { Container, device } from "../Style";
import Styled from "styled-components";
import Divider from "@mui/material/Divider";
import AddIcon from "@mui/icons-material/Add";
import RemoveIcon from "@mui/icons-material/Remove";
import EducationInfoCollapse from "./EducationInfoCollapse";

const StyledTitle = Styled.div`
  color: #000000;
  font-size: 22px;
  font-weight: 500;
  font-family: establishRetrosansOTF;
  margin-bottom: 40px;
`;

const EducationInfoTable = Styled.div`
  display: flex;
  flex-direction: column;
  padding-left: 15%;
  padding-right: 15%;
  max-width: 1000px;

  @media ${device.lg} {
    padding-left: 10%;
    padding-right: 10%;
  }

  @media ${device.md} {
    padding-left: 0;
    padding-right: 0;
  }
`;

const EducationInfoRow = Styled.div`
  color: ${({ collapsed }) => (collapsed ? "#000000" : "#ffffff")};
  background-color: ${({ collapsed }) => (collapsed ? "#ffffff" : "#5DB683")};
  font-size: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 9px;
  padding-bottom: 9px;
  transition: background-color 0.3s ease, color 0.3s ease;
`;

const EducationInfoName = Styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
`;

const EducationInfoButton = Styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;

const CollapseBigRow = Styled.div`
  display: flex;
  flex-direction: column;
  gap: 12px;
`;

const CollapseDescription = Styled.div`
  text-align: left;
  margin-bottom: 25px;
`;

const CollapseSmallRow = Styled.div`
  display: flex;
  flex-direction: row;
  gap: 7px;
`;

const CollapseTitle = Styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-weight: 600;
  flex-direction: row;
  gap: 7px;
  word-break: break-word;
  text-align: left;
  width: 90px;
`;

const CollapseBold = Styled.div`
  font-weight: 600;
  text-align: left;
`;

const CollapseContent = Styled.div`
  display: flex;
  flex-direction: row;
  padding-left: 27px;
  text-align: left;
  gap: 5px;
`;

const educationData = [
  {
    id: 1,
    title: " ",
    description:
      "       .",
    time: "   11",
    location: "1 ",
    leader: " ,  ",
    people: "      ",
  },
  {
    id: 2,
    title: "",
    description:
      " Grade 1 Grade 6     .",
    time: "   11",
    location: "1 ",
    leader: " ,  ",
    people: "       ",
  },
  {
    id: 3,
    title: "",
    description:
      " Grade 7 Grade 12     .",
    time: "   11",
    location: "1  ()",
    leader: " ,  ",
    people:
      "         ",
    people2: "* :  ",
  },
  {
    id: 4,
    title: "",
    description: "    .",
    time: "   6 30",
    location: "1 ",
    leader: " ,  ",
  },
  {
    id: 5,
    title: " ",
    description: "      .",
    time: "   10",
    location: "2  ()",
    leader: " ",
    people: "   ",
    people2: "* :      ",
  },
];

const EducationTable = () => {
  const [expand, setExpand] = useState([]);

  const handleExpand = (id) => {
    setExpand([...expand, id]);
  };

  const handleCollapse = (id) => {
    setExpand(expand.filter((i) => i !== id));
  };

  return (
    <Container>
      <StyledTitle></StyledTitle>
      <EducationInfoTable>
        {educationData.map((r) => (
          <>
            <Divider />
            <EducationInfoRow collapsed={expand.includes(r.id) ? false : true}>
              <EducationInfoName>{r.title}</EducationInfoName>
              <EducationInfoButton>
                <AddIcon
                  style={{
                    color: "#5DB683",
                    weight: 200,
                    cursor: "pointer",
                    display: expand.includes(r.id) ? "none" : "block",
                  }}
                  onClick={() => handleExpand(r.id)}
                />
                <RemoveIcon
                  style={{
                    color: "#ffffff",
                    weight: 200,
                    cursor: "pointer",
                    display: expand.includes(r.id) ? "block" : "none",
                  }}
                  onClick={() => handleCollapse(r.id)}
                />
              </EducationInfoButton>
            </EducationInfoRow>
            <EducationInfoCollapse
              collapsed={expand.includes(r.id) ? false : true}
            >
              <CollapseBigRow>
                <CollapseDescription>{r.description}</CollapseDescription>
                {r.time && (
                  <CollapseSmallRow>
                    <CollapseTitle> </CollapseTitle>
                    <CollapseContent>{r.time}</CollapseContent>
                  </CollapseSmallRow>
                )}
                {r.location && (
                  <CollapseSmallRow>
                    <CollapseTitle> </CollapseTitle>
                    <CollapseContent>{r.location}</CollapseContent>
                  </CollapseSmallRow>
                )}
                {r.leader && (
                  <CollapseSmallRow>
                    <CollapseTitle> </CollapseTitle>
                    <CollapseContent>
                      <CollapseBold></CollapseBold>
                      {r.leader}
                    </CollapseContent>
                  </CollapseSmallRow>
                )}
                {r.people && (
                  <CollapseSmallRow>
                    <CollapseTitle></CollapseTitle>
                    <CollapseContent>{r.people}</CollapseContent>
                  </CollapseSmallRow>
                )}
                {r.people2 && (
                  <CollapseSmallRow>
                    <CollapseTitle></CollapseTitle>
                    <CollapseContent>{r.people2}</CollapseContent>
                  </CollapseSmallRow>
                )}
              </CollapseBigRow>
            </EducationInfoCollapse>
          </>
        ))}
        <Divider />
      </EducationInfoTable>
    </Container>
  );
};

export default EducationTable;
